.display-modal-none {
  display: none;
}
.fontSize-modal-data {
  font-size: 30px;
}

/*  */
.eb-modal-dialog {
  max-width: 690px;
}
.eb-modal-content {
  border-radius: 4px;
}
.eb-modal-header {
  padding: 0 24px;
  background-color: #5ae6a2;
  height: 86px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.header-modal-data {
  position: relative;
  left: 35%;
  margin-bottom: 0;
  font-size: 20px;
  font-weight: bold;
  color: #fff;
}
.eb-modal-body {
  padding: 59px 16px 35px 16px;
  height: 569px;
  overflow-y: auto;
}
.eb-modal-body2 {
  padding: 49px 49px 103px 49px;
}
.btn-modal-data {
  display: block;
  width: 312px;
  height: 71px;
  margin-top: 0px;
  margin-bottom: 24px;
  box-shadow: none;
  font-size: 18px;
  font-weight: bold;
  color: #242b2e;
  background-color: #fff;
  border-radius: 4px;
  border: solid 1px #5ae6a2;
}
.btn-modal-data:hover,
.btn-modal-data:active,
.btn-modal-data:focus {
  color: #fff;
  background-color: #5ae6a2;
  border: solid 1px #5ae6a2;
}

/* other pages */
.eb-modal-icon {
  width: 23px;
  height: 23px;
  margin-right: 1px;
}
.eb-modal-tit1 {
  margin: 0px 10px 0px 0px;
  font-size: 18px;
  color: #242b2e;
}
.eb-modal-input {
  direction: ltr;
  width: 100%;
  height: 71px;
  margin: 10px 0 0 0;
  padding: 0 27px;
  border-radius: 4px;
  border: solid 1px rgba(150, 148, 148, 0.6);
  display: block;
  font-size: 19px;
  /* letter-spacing: -0.29px; */
  color: #242b2e;
  font-family: Roboto;
}
.eb-modal-textarea:focus, .eb-modal-input:focus {
  border: solid 1px #5ae6a2;
}
.eb-modal-input:focus-visible {
  outline: none;
}
.eb-modal-textarea {
  direction: rtl;
  resize: none;
  width: 100%;
  min-height: 161px;
  height: fit-content;
  /* max-height: 290px; */
  margin: 10px 0 0 0;
  padding: 16px 27px;
  border-radius: 4px;
  border: solid 1px rgba(150, 148, 148, 0.6);
  display: block;
  font-size: 18px;
  /* letter-spacing: -0.29px; */
  color: #242b2e;
}
.eb-modal-textarea:focus-visible {
  outline: none;
}
.eb-modal-add1 {
  width: 100%;
  margin-top: 10px;
}
.eb-modal-add {
  display: block;
  width: 17px;
  height: 17px;
  border: none;
  float: right;
  border-radius: 4px;
  margin-right: 0px;
  padding: 0px;
}
.eb-modal-add-img {
  width: 17px;
  height: 17px;
  display: block;
}

.eb-modal-mapbg {
  width: 100%;
  height: 129px;
  margin: 63px 0px 6px 0px;
  opacity: 0.49;
  border-radius: 8px;
  border: solid 1px #707070;
}
.eb-modal-mapbtn {
  width: 39%;
  height: 57px;
  margin: 17px 0px 0 0;
  padding: 0;
  border-radius: 4px;
  border: solid 1px #b294f2;
  background-color: #fff;
  font-size: 17px;
  color: #242b2e;

  position: absolute;
  right: 30%;
  bottom: 40px;
}

/* times */
.eb-modal-right {
  text-align: right;
  text-align: -webkit-right;
  text-align: -moz-right;
  text-align: -ms-right;
  margin-top: 25px;
  margin-right: 12px;
}
.eb-modal-body2::-webkit-scrollbar {
  float: left;
}
.bg-modal {
  background-color: rgb(86, 86, 86, 0.15);
}

.eb-modal-bottom {
  position: absolute;
  bottom: 20px;
  width: 100%;
  margin: 4px -49px 0 -49px;
}
.eb-modal-bottom > hr {
  background-color: #c9c9c9;
  height: 2px;
}
.eb-modal-button {
  width: 163px;
  height: 45px;
  /* margin: 6px 105px 0 266px; */
  /* padding: 4px 24px 3px 20px; */
  border-radius: 4px;
  border: solid 1px #f7f7f7;
  background-color: #eaeaea;

  font-size: 18px;
  letter-spacing: -0.2px;
  color: #969494;
}
.eb-modal-button-active {
  background-color: #c9b2f8 !important;
  color: #fff !important;
}

.eb-modal-bottom2 {
  position: initial;
  /* bottom: 20px;
  width: 100%; */
  padding-top: 0.25px;
  margin: 0px -49px -95px -49px;
}
.eb-modal-bottom2 > hr {
  background-color: #c9c9c9;
  height: 2px;
}
.eb-modal-button2 {
  width: 163px;
  height: 45px;
  /* margin: 6px 105px 0 266px; */
  /* padding: 4px 24px 3px 20px; */
  border-radius: 4px;
  border: solid 1px #f7f7f7;
  background-color: #eaeaea;
  font-size: 20px;
  letter-spacing: -0.2px;
  color: #969494;
}
.eb-modal-backimg {
  /* width: 23px;
  height: 22.4px;; */
  width: 23px;
  height: 19.5px;
  cursor: pointer;
}
.eb-modal-closeimg {
  width: 17px;
  height: 17px;
  cursor: pointer;
}
