/* amiin */
.selectJOB {
  width: 95px;
  height: 43px;
  opacity: 0.5;
  border-radius: 4px;
  border: solid 1px #c9c9c9;
  margin-left: 22px;
  padding: 0 6px;
  background-color: #fff;
}
.selectJOB:focus,
.selectJOB:focus-visible {
  border: solid 1px #565656;
}
.selectJOB:focus-visible {
  outline: none;
}
.selectJOB:disabled {
  opacity: 0.35;
}
.selectJOB > option {
  font-size: 14px;
  color: #242b2e;
}

.selectJOB:after {
  margin: 0 10px;
  position: absolute;
  content: "";
  top: 14px;
  right: 10px;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-color: rgb(123, 23, 23) transparent transparent transparent;
}

