/* Create a custom checkbox */

/* The container */
.container-s5 {
  /* display: block; */
  width: fit-content;
  position: relative;
  /* padding-right: 16px;
  margin-bottom: 10px; */
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  text-align: right;
  color: #242b2e;
  margin-top: 20px;
  margin-bottom: 30px;
}

/* Hide the browser's default checkbox */
.container-s5 input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.s5-24h {
  /* position: absolute;
  top: 0;
  right: 0; */
  height: 15px;
  width: 15px;
  background-color: #fff;
  border-radius: 2px;
  border: solid 1px #565656;
  margin-left: 6px;
}
/* When the checkbox is checked, add a blue background */
.container-s5 input:checked ~ .s5-24h {
  background-color: #5ae6a2;
  border: solid 0px #5ae6a2;
}

/* Create the checkmark/indicator (hidden when not checked) */
.s5-24h:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.container-s5 input:checked ~ .s5-24h:after {
  display: block;
}

