@media only screen and (max-width: 767.5px) {
  .modal-dialog-centered.eb-modal-dialog{
    justify-content: center;
  }
  .eb-modal-content {
    max-width: 338px;
}
  /* .display-modal-none {
    display: none;
  } */
  .eb-modal-dialog {
    max-width: 90%;
  }
  .eb-modal-header {
    padding: 0 16px;
    height: 70px;
    justify-content: center;
  }
  .header-modal-data {
    left: 0%;
    font-size: 20px;
  }
  .eb-modal-body {
    padding: 35px 16px 19px 16px;
    min-height: fit-content;
    height: 410px;
    overflow-y: auto;
  }
  .eb-modal-body2 {
    padding: 20px 20px 103px 20px;
  }
  .btn-modal-data {
    width: 85%;
    height: 55px;
    margin-bottom: 16px;
    box-shadow: none;
    font-size: 16px;
    padding: 0;
  }
  .btn-modal-data:hover,
  .btn-modal-data:active,
  .btn-modal-data:focus {
    color: #fff;
    background-color: #5ae6a2;
    border: solid 1px #5ae6a2;
  }
  
  /* other pages */
  .eb-modal-icon {
    width: 20px;
    height: 20px;
    margin-right: 1px;
  }
  .eb-modal-tit1 {
    margin: 0px 6px 0px 0px;
    font-size: 16px;
    color: #242b2e;
  }
  .eb-modal-input {
    width: 100%;
    height: 50px;
    margin: 10px 0 0 0;
    padding: 0 10px;
    border-radius: 4px;
    border: solid 1px rgba(150, 148, 148, 0.6);
    display: block;
    font-size: 14px;
    /* letter-spacing: -0.29px; */
    color: #242b2e;
    font-family: Roboto;
  }
  .eb-modal-input:focus-visible {
    outline: none;
  }
  .eb-modal-textarea {
    direction: rtl;
    resize: none;
    width: 100%;
    min-height: 161px;
    height: fit-content;
    /* max-height: 290px; */
    margin: 10px 0 0 0;
    padding: 10px 10px;
    font-size: 14px;
  }

  .eb-modal-textarea:focus-visible {
    outline: none;
  }
  .eb-modal-add1 {
    width: 100%;
    margin-top: 10px;
  }
  .eb-modal-add {
    width: 16px;
    height: 16px;
    border: none;
    float: right;
    border-radius: 4px;
    margin-right: 0px;
    padding: 0px;
  }
  .eb-modal-add-img {
    width: 16px;
    height: 16px;
    display: block;
  }
  
  .eb-modal-mapbg {
    width: 100%;
    height: 130px;
    margin: 25px 0px 25px 0px;
    opacity: 0.49;
    border-radius: 8px;
    border: solid 1px #707070;
  }
  .eb-modal-mapbtn {
    width: fit-content;
    height: 44px;
    margin: 0px 0px 0 0;
    padding: 0 12px;
    border-radius: 4px;
    font-size: 15px;
    position: absolute;
    right: 24%;
    bottom: calc(24% + 20px);
  }
  
  /* times */
  .eb-modal-right {
    text-align: right;
    text-align: -webkit-right;
    text-align: -moz-right;
    text-align: -ms-right;
    margin-top: 25px;
    margin-right: 12px;
  }
  .eb-modal-body2::-webkit-scrollbar {
    float: left;
  }
  .bg-modal {
    background-color: rgb(86, 86, 86, 0.15);
  }
  
  .eb-modal-bottom {
    position: absolute;
    bottom: 20px;
    width: 100%;
    margin: 4px -20px 0 -20px;
    padding: 0 20px;
  }
  .eb-modal-bottom > hr {
    height: 1px;
    margin: 16px -20px 16px -20px;

  }
  .eb-modal-button {
    width: 100%;
    height: 45px;
    font-size: 18px;
    letter-spacing: -0.2px;
    padding: 0;
  }

  
  .eb-modal-bottom2 {
    position: unset;
    bottom: 20px;
    /* width: 100%; */
    padding-top: 0.25px;
    margin: 0px -20px -93px -20px;
    padding: 3.25px 20px 0 20px;
   
  }
  .eb-modal-bottom2 > hr {
    height: 1px;
    margin: 16px -20px 16px -20px;
  }
  .eb-modal-button2 {
    width: 100%;
    height: 45px;
    font-size: 18px;
    letter-spacing: -0.2px;
    padding: 0;
  }
  .eb-modal-backimg {
    width: 19px;
    height: 16.5px;
    cursor: pointer;
    position: absolute;
    right: 16px;
  }
  .eb-modal-closeimg {
    width: 14px;
    height: 14px;
    cursor: pointer;
    position: absolute;
    right: 16px;
  }
  .MuiPaper-root.MuiPaper-rounded.MuiPopover-paper {
    margin-top: 8px !important;
}
.s5-res-w-res{
  padding-right: 0px !important;
}
  
}
