.res-card {
  box-shadow: none;
  border-radius: 2px;
  border: solid 1px rgba(112, 112, 112, 0.12);
  min-height: 230px;
  height: fit-content;
  text-align: right;
}
.res-card-img {
  object-fit: cover;
  width: 100%;
  min-height: 230px;
  height: 100%;
  border-radius: 0px 2px 2px 0px;
}
.res-card .card-body {
  padding: 18px;
  color: #101010;
}
.res-card-tit {
  display: flex;
  justify-content: space-between;
  margin: 0px 0px 2px 5px;
  font-size: 18px;
  font-weight: bold;
  text-align: right;
}
.res-card-tit-img {
  width: 19px;
  height: 19px;
  border-radius: 50%;
}
.res-card-text1 {
  margin: 4px 0px 2px 0px;
  font-size: 15px;
  font-stretch: expanded;
  letter-spacing: -0.27px;
  text-align: right;
}
.res-card-text2 {
  opacity: 0.53;
  font-size: 13px;
  font-weight: 300;
  line-height: 1.31;
  letter-spacing: -0.23px;
  text-align: right;
  margin-bottom: 2px;
}
.res-card-text3 {
  max-width: 412px;
  margin: 8px 0 0 0;
  /* padding: 17px 374px 0 29px; */
  font-size: 13px;
  font-weight: 300;
  letter-spacing: -0.23px;
  text-align: right;
}
.res-card-text3 span {
  color: #5ae6a2;
}
.res-card-box2 {
  float: right;
}
.res-card-box {
  display: flex;
  justify-content: right;
  /* margin: 10px 0 0 0; */
}
.res-card-box p {
  width: fit-content;
  height: 17px;
  padding: 0 7px;
  margin: 0px 0px 8px 6px;
  border-radius: 6px;
  background-color: #eaeaea;
  font-size: 11px;
  font-weight: 300;
  line-height: 1.55;
  letter-spacing: -0.2px;
  text-align: right;
  color: #242b2e;
}
@media only screen and (max-width: 767.5px) {
    .res-card{
    max-width: 334px;

    }
    .res-card-a{
        display: block;
        width: fit-content;
    }
    .res-card-img {
        max-height: 220px;
        min-height: 215px;
      }
  .res-card .card-body {
    padding: 12px 14px 18px;
  }
  .res-card-box p {
    margin: 8px 0px 4px 6px;
  }
  .res-card-tit-img {
    width: 17px;
    height: 17px;
    margin-top: 12px;
  }

  .res-card-tit {
    font-size: 15px;
  }
  .res-card-text1 {
    font-size: 12px;
    margin: -7px 0px 2px 0px;
  }
  .res-card-text2 {
    font-size: 10px;
  }
}
